body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header .navbar-expand-lg .navbar-collapse {
  justify-content: flex-end;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.rec-arrow:hover,.rec-arrow:focus{
  background-color: rgb(254 145 145) !important;
    box-shadow: 0 0 2px 0 #fe9191 !important;
}
.carousel-item{
  .row{
    align-items: center;
  }
}

.breadcrumb-area{
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(140,174,219,1) 100%);
   position: relative;
}
.rltv{
  position: relative;
}
.routes{
  min-height: 100vh;
}

button:active,a:active{
  opacity: 0.4;
}

.service{

  .main-btn{
    margin-top: 30px !important;
    margin: auto;
  }
}
.appointment{
  flex: 1;
  padding: 20px;
  h6{
    margin-bottom: 25px;
  }
.form_rdv{
  display: flex;
}
  input{
    border: 1px solid #c7c7c7;
    height: 50px;
    width: 50%;
    border-radius: 30px;
    padding-left: 20px;
  }
  .input_mail{
    width: 80%;
  }
  button{
    font-size: 15px;
    border-radius: 40px;
    font-weight: 400;
    padding: 15px 20px;
    color: #FFFFFF;
    background: rgba(255, 134, 134, 0.9);
  }
}

.appointment-section .appointment-content{
  background: #ffffff8c;
}

.service{
  background: #f7f7f7b0;
    position: relative;
}

.background_app{
position: fixed;
    z-index: 0;
    left: 0;
    width: 100%;
    top: 0;
    bottom: 0;
    filter: opacity(0.08);
}

.common-title{
  h2{
    margin: auto !important;
  }
}

.presentation-div{
  p{
    svg{
      color: #ff9292;
    font-size: 33px;
    margin-top: -3px;
    margin-right: 8px;
    }
  }
}

.service-div {
  position: absolute;
  z-index: 10000;
  bottom: 20px;
  background: #ffffffba;
  width: 100%;
  margin: 0;
  bottom: 0;
  height: 88px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  h3{
    font-size: 19px !important;
    font-weight: 700 !important;
  }
}
@media (min-width: 1400px)
{
  .container {
    max-width: 1400px !important;
}
@media (min-width: 1600px)
{
  .container {
    max-width: 1600px !important;
  }
}
 

 
.article{
  width: 400px !important;
  height: 400px !important;
}
  .navbar-area .nav-container .navbar-collapse .navbar-nav li a {
    font-size: 20px !important;
  }
}

